@charset "utf-8";

.cont{
  margin:0 auto;
}
h2{
  text-align:center;
  font-size:28px;
  margin-top:50px;
  color:#333333;
}
.tit2{
  text-align:center;
  font-size:16px;
  color:#999999;
  margin: 20px 0px;
}
.cont1{
  width:1200px;
  margin:0 auto;
}
ul{
  display: flex;
  flex-wrap:nowrap;
  justify-content: space-between;
}
ul li {
  width:280px;
  position: relative;
  line-height: 20px;
}
ul li img{
  width:100%;
  margin:10px 0px;

}
.name{
  line-height: 24px;
  width:280px;
  overflow: hidden;
  text-overflow: ellipsis;
  display:-webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.shop{
  padding:8px 0px;
  text-align: center;
  border: 1px solid #999;
  cursor: pointer;
  margin-bottom: 10px;
}
.price{
  color:#ccc;
  text-decoration: line-through;
}
.event{
  background-color: rgb(189, 118, 255);
  padding:100px ;
  border-radius: 3px;
  border:none;
  padding:7px;
  color:#fff;
  text-align: center;
  position: absolute;
  top:0px;
  margin:20px 8px;
}
.sale , .price2{
  font-weight: bold;
}
.sale{
  color:rgb(250, 98, 47)
}
.cart_icon{
  font-size: 18px;
  display: inline-block;
  transform: translateY(3px);
  padding-right: 5px;
}
.total{
  margin-bottom:30px;
}